import React, { PureComponent } from 'react'
import {withRouter} from 'react-router-dom'
import { withStyles } from '@material-ui/styles'
import Grid from '@material-ui/core/Grid'
import Typography from '@material-ui/core/Typography'
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemAvatar from '@material-ui/core/ListItemAvatar';
import ListItemText from '@material-ui/core/ListItemText'
import Button from '@material-ui/core/Button'

import TouXiang from '../assets/images/pic_touxiang.png'
import ZiLiao from '../assets/images/icon_ziliao.png'
import AboutMe from '../assets/images/me_icon_about.png'
import FAQ from '../assets/images/me_icon_FAQ.png'

import MainLayout from '../layout/MainLayout';

const styles = {
	container: {
		padding: '18px 8px',
		background: '#fff',
	},
	avatar: {
		padding: '0 16px',
		'& img': {
			width: '42px'
		}
	},
	personInfo: {
		height: '60px',
		padding: '0 10px'
	},

	list: {
		padding: '0 10px',
	},

	listItem: {
		background: '#fff',
		margin: '1rem 0',
		padding: '0.625rem 1rem',
		borderRadius: '8px',
		'& img': {
			width: '1.6rem'
		},
	},
	listIcon: {
		minWidth: '42px',
	}
}

class My extends PureComponent {

	state = {
		selectedIndex: -1,
	}

	handleListItemClick = (e, index) => {
		const {history}=this.props;
		switch (index) {
			case 0:
				break;
			case 1:
				break;
			case 2:
				history.push('./faq');
				break;
			default:
				break;
		}
	}

	render() {
		const { classes } = this.props;

		return (
			<MainLayout>
				<Grid
					container
					direction="row"
					justify="flex-start"
					alignItems="center"
					className={classes.container}
				>
					<Grid item className={classes.avatar}>
						<img src={TouXiang} />
					</Grid>
					<Grid item>
						<Grid container
							direction="column"
							justify="space-around"
							className={classes.personInfo}
						>
							<Grid item>
								<Typography color="textSecondary">User name</Typography>
							</Grid>
							<Grid item>
								<Typography variant="h6">Belum masuk</Typography>
							</Grid>
						</Grid>
					</Grid>
				</Grid>
				<List className={classes.list} >
					<ListItem button
						onClick={event => this.handleListItemClick(event, 0)}
						className={classes.listItem}>
						<ListItemAvatar className={classes.listIcon}>
							<img src={ZiLiao} />
						</ListItemAvatar>
						<ListItemText primary="Profil" />
					</ListItem>
					<ListItem button
						onClick={event => this.handleListItemClick(event, 1)}
						className={classes.listItem}>
						<ListItemAvatar className={classes.listIcon} >
							<img src={AboutMe} />
						</ListItemAvatar>
						<ListItemText primary="Tentang Maucash" />
					</ListItem>
					<ListItem button
						onClick={event => this.handleListItemClick(event, 2)}
						className={classes.listItem}>
						<ListItemAvatar className={classes.listIcon}>
							<img src={FAQ} />
						</ListItemAvatar>
						<ListItemText primary="Pusat Bantuan" />
					</ListItem>
				</List>
			</MainLayout>
		)
	}
}

export default withStyles(styles)(withRouter(My));
