/* eslint-disable no-unused-vars */

import React, { PureComponent } from 'react';
import { BrowserRouter,Route } from 'react-router-dom';
import { withStyles } from '@material-ui/styles';
import { ThemeProvider } from '@material-ui/styles';
import { createMuiTheme } from '@material-ui/core/styles';
import Home from './pages/Home';
import Loan from './pages/Loan';
import My from './pages/My';
import FAQ from './pages/FAQ';
import FAQQuestion from './pages/FAQQuestion';
import FAQAnswer from './pages/FAQAnswer';

import {SERVER_PATH} from './config'

const theme = createMuiTheme({
  typography: {
    h6: {
      color: '#3e3e3e'
    }
  },
  props:{
     MuiButtonBase:{
        //是否涟漪效果
        disableRipple:false
     }
  }
})

const styles = theme => ({
  app: {
    height: '100%',
    background: '#f5f5f5'
  }
})


class App extends PureComponent {
  render() {
    const { classes } = this.props;

    return (
      <div className={classes.app}>
        <BrowserRouter basename={SERVER_PATH[process.env.NODE_ENV]}>
          <ThemeProvider theme={theme}>
            <Route path="/"   exact component={Home} />
            <Route path="/home"  component={Home} />
            <Route path="/loan"  component={Loan} />
            <Route path="/my" component={My} />
            <Route path="/faq" exact component={FAQ} />
            <Route path="/faq/question"  component={FAQQuestion} />
            <Route path="/faq/answer"  component={FAQAnswer} />
          </ThemeProvider>
        </BrowserRouter>
      </div>
    )
  }
}

export default withStyles(styles)(App);
