/* eslint-disable no-unused-vars */
import React, { PureComponent } from 'react'
import { withRouter } from 'react-router-dom'
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography'

import NavLayout from '../layout/NavLayout'
import IconAnswer from '../assets/images/icon_answer.png'



class FAQAnswer extends PureComponent {

	state = {
		question: '',
		answer: '',
	}

	componentDidMount() {
		const { question, answer } = this.props.history.location.state;
		this.setState({
			question: question,
			answer: answer
		})
	}

	render() {
		const { question, answer } = this.state;

		return (
			<NavLayout title="FAQ">
				<Grid container direction="column" style={{ background: '#fff', padding: '1rem',borderTop:'1px solid #f5f5f5' }}>
					<Grid container direction="row">
						<Grid item xs={1}><img src={IconAnswer} width="24px" /></Grid>
						<Grid item xs={11}><Typography variant="h6" style={{ wordBreak: 'break-all' }}> {question}</Typography></Grid>
					</Grid>
					<Grid container direction="row" style={{ padding: '1rem 0' }}>
						<Grid item xs={1}></Grid>
						<Grid item xs={11}>
							<Typography style={{ wordBreak: 'break-all' }} variant="body1" dangerouslySetInnerHTML={{__html:answer}} >
							</Typography>
						</Grid>
					</Grid>
				</Grid>
			</NavLayout>
		)
	}
}
export default withRouter(FAQAnswer);
