import React, { PureComponent } from 'react'
import { withRouter } from 'react-router-dom'
import { withStyles } from '@material-ui/styles'
import NavLayout from '../layout/NavLayout'
import List from '@material-ui/core/List'
import ListItem from '@material-ui/core/ListItem'
import ListItemText from '@material-ui/core/ListItemText'

import IconChevronRight from '@material-ui/icons/ChevronRight'
import {getQuestions} from '../api/my/faq'

const styles = {
	list: {
		padding: '0 10px',
	},

	listItem: {
		background: '#fff',
		margin: '1rem 0',
		padding: '0.625rem 1rem',
		borderRadius: '8px',
		'& img': {
			width: '1.6rem'
		},
	},
	listIcon: {
		minWidth: '42px',
	}
}

class FAQQuestion extends PureComponent {

	state={
		isApp:false,
		questions:[],
		typeName:''
	}


	async componentDidMount(){
		let questions=[];
		const search = this.props.history.location.search;
		const searchPrams =new URLSearchParams(search.substring(1))
		const questionType=searchPrams.get('questionType')
		const typeName=searchPrams.get('typeName')
		const isApp=searchPrams.get('isApp');
		
        if(questionType){
			const response =await getQuestions({questionType:questionType});
			questions=response.result.data;
		}
	
		this.setState({
			isApp:isApp,
			questions:questions,
			typeName:typeName
		})
	}

	handleListItemClick = (question, answer) => {
		const { history } = this.props;
		history.push('/faq/answer', { question: question, answer: answer })
	}

	render() {
		const { classes } = this.props;
        const {questions,isApp,typeName} =this.state;
		return (
			<NavLayout title={typeName?typeName:'Bantuan'} sendAppClose={isApp} >
				<List component="nav" className={classes.list}>
					{
						questions.map(question => (
							<ListItem
								button
								key={question.id}
								className={classes.listItem}
								onClick={() => this.handleListItemClick(question.question, question.answer)}
							>
								<ListItemText key={question.id} primary={question.question} />
								<IconChevronRight color="disabled" />
							</ListItem>
						))
					}
				</List>
			</NavLayout>
		)
	}
}
export default withStyles(styles)(withRouter(FAQQuestion));
