/* eslint-disable jsx-a11y/alt-text */
/* eslint-disable no-unused-vars */
import React, { PureComponent } from 'react'
import { withStyles } from '@material-ui/styles';
import {withRouter} from 'react-router-dom';
import Card from '@material-ui/core/Card';
import Typography from '@material-ui/core/Typography';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import Divider from '@material-ui/core/Divider'
import Grid from '@material-ui/core/Grid'
import GridList from '@material-ui/core/GridList'
import GridListTile from '@material-ui/core/GridListTile'
import ButtonBase from '@material-ui/core/ButtonBase'


import PicFAQ from '../assets/images/pic_faq.png'
import IconProdduk from '../assets/images/icon_prodduk.png'
import IconPenCaiRan from '../assets/images/icon_pencairan.png'
import IconPemBayaran from '../assets/images/icon_pembayaran.png'
import IconYinSi from '../assets/images/icon_yinsi.png'
import IconTiaoyue from '../assets/images/icon_tiaoyue.png'
import IconGenDuo from '../assets/images/icon_gengduo.png'

import NavBackIcon from '../components/NavBackIcon'

import {getQuestionType} from '../api/my/faq'

const styles = {
	root: {
		display: 'flex',
		flexWrap: 'wrap',
		minWidth: 300,
		width: '100%',
	},
}

const questionTypes = [
	{
		id:1,
		icon: IconProdduk,
		type: 'Pilihan Produk',
		code:'product_choice'
	},
	{
		id:2,
		icon: IconPenCaiRan,
		type: 'Pencairan',
		code:'disbursement'
	},
	{
		id:3,
		icon: IconPemBayaran,
		type: 'Pembayaran',
		code:'payment'
	},
	{
		id:4,
		icon: IconYinSi,
		type: 'Kendala Aplikasi ',
		code:'application_contraint'
	},
	{
		id:5,
		icon: IconTiaoyue,
		type: 'Syarat Meminjam',
		code:'borrowing_terms'
	},
	{
		id:6,
		icon: IconGenDuo,
		type: 'Lain-lain',
		code:'others'
	},
]
class FAQ extends PureComponent {

	state={
		tileData:[]
	}

	async componentDidMount(){
		const response=await getQuestionType()
		const data=response.result;
		const tileData=questionTypes.filter(item=>{
            return data.some((d)=>{
				return d.typeName===item.code&&d.status===1
			})
		})
		this.setState({
			tileData:tileData
		})

	}

	handleClick=(id,name)=>{
		const {history}=this.props;
		history.push(`./faq/question?questionType=${id}&typeName=${name}`);
	}

	render() {
        const {tileData}=this.state;
		return (
			<div>
                <NavBackIcon />
				<Card elevation={0} style={{ borderRadius: 0 }} >
					<CardMedia
						style={{ borderRadius: 0,objectFit:'fill'}}
						component="img"
						height="200px"
						image={PicFAQ}
					>
					</CardMedia>
					<CardContent>
						<Grid container style={{ margin: '10px 0 16px' }}>
							<Grid item xs={3}><Divider variant="fullWidth" style={{ marginTop: '10px' }} /></Grid>
							<Grid item xs={6} style={{ textAlign: 'center' }}><Typography variant="subtitle2" color="textSecondary" gutterBottom>Kategori Pertanyaan</Typography></Grid>
							<Grid item xs={3}><Divider variant="fullWidth" style={{ marginTop: '10px' }} /></Grid>
						</Grid>
						<GridList 	 cellHeight={90} cols={9}>
							{
								tileData.map(tile =>
									(
											<GridListTile key={tile.type}  style={{textAlign:'center'}} cols={3}>
                                               <ButtonBase onClick={()=>this.handleClick(tile.id,tile.type)} style={{width:'100%'}}>
												<Grid container direction='column'>
													<Grid item style={{ textAlign: 'center' }}>
														<img src={tile.icon} width="40px" />
													</Grid>
													<Grid item style={{ textAlign: 'center' }}>
														<Typography variant="subtitle2">{tile.type}</Typography>
													</Grid>
												</Grid>
												</ButtonBase>
											</GridListTile>
									)
								)
							}
						</GridList>
					</CardContent>
				</Card>
			</div>
		)
	}
}

export default withStyles(styles)(FAQ);