import React, { PureComponent } from 'react'
import { withRouter } from 'react-router-dom'
import { withStyles } from '@material-ui/styles'
import AppBar from '@material-ui/core/AppBar'
import Toolbar from '@material-ui/core/Toolbar'
import Typography from '@material-ui/core/Typography'
import Container from '@material-ui/core/Container'
import IconButton from '@material-ui/core/IconButton'
import IconArrowBackIos from '@material-ui/icons/ArrowBackIos'

const styles = {
	root: {
		flexGrow: 1,
	},
};

class NavBar extends PureComponent {

	handleClick = () => {
		const { history ,sendAppClose} = this.props;
		if(sendAppClose){
			//通知App退出
			window.postMessage('close')
		}else{
			history.go(-1);
		}
	}

	render() {
		const { title, classes } = this.props;
		return (
			<div className={classes.root}>
				<AppBar color="default" elevation={0} style={{background:'#fff'}}  >
					<Toolbar style={{padding:0}}>
						<IconButton onClick={this.handleClick} style={{ position:"absolute",color: '#fff' }}>
							<IconArrowBackIos  color="action" />
						</IconButton>
						<Container style={{textAlign:'center'}}>
						   <Typography variant="h6" color="textPrimary">
						     {title}
				     	   </Typography>
						</Container>
					</Toolbar>
				</AppBar>
			</div>
		)
	}
}

export default withStyles(styles)(withRouter(NavBar));