/* eslint-disable no-unused-vars */
import React, { PureComponent } from 'react'
import { withStyles } from '@material-ui/styles';
import BottomNavigation from '@material-ui/core/BottomNavigation';
import BottomNavigationAction from '@material-ui/core/BottomNavigationAction';
import HomeIcon from '@material-ui/icons/HomeOutlined';
import ScheduleIcon from '@material-ui/icons/ScheduleOutlined';
import PersonOutlineIcon from '@material-ui/icons/PersonOutlined';
import { withRouter } from 'react-router-dom'


const styles = {
	bottomBar: {
		position: 'fixed',
		bottom: 0,
		left: 0,
		borderTop: '1px solid #eeedef',
		background: '#fff',
		width: '100%',
	}
}

const CustomBottomNavigationAction = withStyles({
    root:{
		color:'#bbb'
	},
	label: {
		fontSize: '0.68rem',
		color: '#bbb',
	},
	selected: {
		fontSize: '0.68rem !important',
		color:'#6e9ee5 !important',
	},
	wrapper: {
		fontSize: '30px',
	},


})(BottomNavigationAction)

class BottomBar extends PureComponent {

	componentDidMount() {
		this.initCurActiveIndex(this.props.history.location.pathname)
	}

	componentDidUpdate() {
		this.initCurActiveIndex(this.props.history.location.pathname)
	}

	state = {
		curIndex: 0
	}

	//设置当前菜单选中
	initCurActiveIndex(pathname) {
		let index = 0;
		switch (pathname) {
			case '/':
			case '/home':
				index = 0;
				break;
			case '/loan':
				index = 1;
				break;
			case '/my':
				index = 2;
				break;
			default:
				break;
		}
		this.setState({
			curIndex: index
		})
	}

	handleChange = (event, index) => {
		this.setState({
			curIndex: index,
		})
		switch (index) {
			case 0:
				this.props.history.push('/home')
				break;
			case 1:
				this.props.history.push('/loan')
				break;
			case 2:
				this.props.history.push('/my')
				break;
			default:
				break;
		}

	}

	render() {
		const { curIndex } = this.state;
		const { classes } = this.props;

		return (
			<div className={classes.bottomBar}>
				{
					<BottomNavigation value={curIndex} onChange={this.handleChange} showLabels>
						<CustomBottomNavigationAction label='Beranda' icon={<HomeIcon fontSize='inherit' color="inherit" />} />
						<CustomBottomNavigationAction label='Pinjaman' icon={<ScheduleIcon fontSize='inherit' />} />
						<CustomBottomNavigationAction label='Profil' icon={<PersonOutlineIcon fontSize='inherit' />} />
					</BottomNavigation>
				}
			</div>
		)
	}
}
export default withStyles(styles)(withRouter(BottomBar))