import axios from "axios";
import {API_URL} from '../config'


//创建axios实例
const instance = axios.create({
  baseURL: API_URL[process.env.NODE_ENV],
  timeout: 60000,
  headers: {
    "Content-Type": "application/json; charset=utf-8"
  }
});

//请求拦截器
instance.interceptors.request.use(
  config => {
    config.headers.common["X-User-Token"] = ''
    config.headers.common["X-Org-Id"] = ''
    return config;
  },
  error => {
    // Message({
    //   message: "接口请求失败! " + error.message,
    //   type: "error",
    //   duration: 2000
    // });
    Promise.reject(error);
  }
);

//返回拦截器
instance.interceptors.response.use(
  response => {
    let data = response.data;
    return data;
  },
  error => {
    Promise.reject(error);
  }
);

/**
 * @class Http请求
 */
class Xhr {

  /**
   * @method GET请求API接口
   * @param {String} url 请求地址
   * @param {Object} params 请求参数
   */
  async get(url, params = {}) {
    console.log(API_URL[process.env.NODE_ENV])
    let data = await instance({
      url: url,
      method: "get",
      params: params
    });
    return data;
  }

  /**
   * @method POST请求API接口
   * @param {String} url 请求地址
   * @param {Object} params 请求参数
   */
  async post(url, params = {}) {
    let data = await instance({
      baseURL: process.env.API_URL,
      url: url,
      method: "post",
      data: params
    });
    return data;
  }

  /**
   * @method DELETE请求API接口
   * @param {String} url 请求地址
   * @param {Object} params 请求参数
   */
  async delete(url, params = {}) {
    let data = await instance({
      url: url,
      method: "delete",
      data: params
    });
    return data;
  }

  /**
   * @method DOWNLOAD请求API接口
   * @param {String} url 请求地址
   * @param {Object} params 请求参数
   */
  // async download(url, params = {}) {
  //   let data = await instance({
  //     url: url,
  //     method: "get",
  //     params: params,
  //     responseType: "blob"
  //   });
  //   let a = document.createElement("a");
  //   let fileurl = window.URL.createObjectURL(data);
  //   let filename = params.ExportName ? params.ExportName : "download";
  //   a.href = fileurl;
  //   a.download = filename;
  //   a.click();
  //   window.URL.revokeObjectURL(fileurl);
  // }

  /**@method 下载静态文件 */
  async downloadStatic(url, name) {
    let userAgent = navigator.userAgent;
    if (userAgent.indexOf("Chrome") > -1) {
      let res = await axios.get(url, { responseType: "blob" });
      let a = document.createElement("a");
      let fileurl = window.URL.createObjectURL(res.data);
      let filename = name ? name : "voice.wav";
      a.href = fileurl;
      a.download = filename;
      a.click();
      window.URL.revokeObjectURL(fileurl);
    } else {
      window.open(url, "_bank");
    }
  }

}

export default new Xhr();
