import request from '../request'

//根据获取问题类型
export const getQuestionType=async ()=>{
	let data=await request.get('/welab-marketing/api/v1/noauth/app/getQuestionTypeList')
	return data;
}

//根据获取问题列表与答案
export const getQuestions=async (params)=>{
	let data=await request.post('/welab-marketing/api/v1/noauth/app/getQuestionList',{queryParam:params})
	return data;
}