import React, { PureComponent } from 'react'
import {withRouter} from 'react-router-dom'
import IconButton from '@material-ui/core/IconButton'
import IconArrowBackIos from '@material-ui/icons/ArrowBackIos'

class NavBackIcon extends PureComponent {

	handleClick=()=>{
		const {history}=this.props;
		history.go(-1);
	}

	render() {
		return (
			<IconButton onClick={this.handleClick} style={{ position: "absolute", color: '#fff' }}>
				<IconArrowBackIos />
			</IconButton>
		)
	}
}

export default withRouter(NavBackIcon)