import React, { Component } from "react";
import { withStyles } from '@material-ui/styles'
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import banner1 from '../assets/images/pic_faq.png'

const styles = {
  slider:{
     '& .slick-dots':{
      bottom:'6px',
      '& li.slick-active button:before':{
        color:'#fff'
      }
     }
  },
}

class Banner extends Component {
  render() {
    const { classes } = this.props;
    const settings = {
      autoplay: true,
      autoplaySpeed: 6000,
      arrows: false,
      dots: true,
      infinite: false,
      speed: 500,
      slidesToShow: 1,
      slidesToScroll: 1
    };
    return (
      <Slider style={{ heigth: '180px' }}  className={classes.slider}  {...settings}>
        <div>
          <img src={banner1} width="100%" style={{ borderRadius: '6px' }} />
        </div>
        <div>
          <img src={banner1} width="100%" style={{ borderRadius: '6px' }} />
        </div>
        <div>
          <img src={banner1} width="100%" style={{ borderRadius: '6px' }} />
        </div>
      </Slider>
    );
  }
}

export default withStyles(styles)(Banner);